import React, {useState, useEffect, useCallback} from "react";
import { Container, Row, Col, Form, Pagination, Dropdown, Button } from "react-bootstrap";
import filtros from "../components/actions/filtrosFunctions";
import RangeSlider from 'react-range-slider-input';
import ReactPaginate from "react-paginate";

import Layout from "../components/layout";
import Seo from "../components/seo";
import ListView from "../components/VistaCatalogo/ListView";
import GridView from "../components/VistaCatalogo/GridView";

import api from '../components/actions/riveroapi';
import PlaceHolderCar from "../components/VistaCatalogo/placeholderCar";

import FilterLayout from "../components/layout/filterLayout";
import { CiGrid41, CiGrid2H } from "react-icons/ci";

import { navigate } from "gatsby";

export default function Chevrolet (props) {

  const [ car, setCar ] = useState();
  const [ newCars, setnewCars ] = useState();
  const [ loading, setLoading ] = useState(true);
  const [active, setActive] = useState(1);
  const [ value, setValue ] = useState(0); 

  const [itemOffset, setItemOffset] = useState(0);
  const [ search, setSearch ] = useState();

  const [ dataInventario, setDataInventario ] = useState([]);
  const [ dataAnio, setDataAnio ] = useState([]);
  const [ dataUnidad, setDataUnidad ] = useState([]);
  const [ dataMarca, setDataMarca ] = useState([]);
  const [ dataModelo, setDataModelo ] = useState([]);

  const [ clickList, setClickList ]  = useState(false);

  const [ datosFiltros, setDatosFiltros ] = useState({
    anios: [],
    marcas: [],
    modelo: [],
    tipoUnidad: [],
  });

  var tipo = [];
  var anio = [];
  var marca = [];
  var modelo = [];
  var unidad = [];
  var precio = [];

  //UseEffecto Solo habla a funcion herramienta.
  useEffect(() => {  herramienta();  }, []);

  const endOffset = itemOffset + 12;
  //console.log(`Loading items from ${itemOffset} to ${endOffset}`);
  const currentItems = car?.slice(itemOffset, endOffset);
  const pageCount = Math.ceil(car?.length / 10);

  const currentItemsNew = newCars?.slice(itemOffset, endOffset);
  const pageCountNew = Math.ceil(newCars?.length / 10);

  const handlePageClick = (event) => {
    const newOffset = (event.selected * 10) % car?.length;
    //console.log(`User requested page number ${event.selected}, which is offset ${newOffset}` );

    setItemOffset(newOffset);
  };

  async function herramienta(){

    const urlParams = new URLSearchParams(props?.location?.search); 

    const options = {
      type: urlParams.get("type") ? urlParams.get("type") : "",
      year: urlParams.get("year") ? urlParams.get("year") : "",
      unity: urlParams.get("unity") ? urlParams.get("unity") : "",
      make: urlParams.get("make") ? `${urlParams.get("make")}` : "chevrolet,gmc,cadillac,buick",
      price: urlParams.get("price") ? urlParams.get("price") : "",
      search: urlParams.get("search") ? urlParams.get("search") : "",

      orderBy: "precio:asc",
      ultimoModelo: true,
      perPage: 50,
    };

    const optionsSeminuevos = {
      type: urlParams.get("type") ? urlParams.get("type") : "",
      year: urlParams.get("year") ? urlParams.get("year") : "",
      unity: urlParams.get("unity") ? urlParams.get("unity") : "",
      make: urlParams.get("make") ? urlParams.get("make") : "",
      price: urlParams.get("price") ? urlParams.get("price") : "",
      search: urlParams.get("search") ? urlParams.get("search") : "",

      orderBy: "precio:asc",
      ultimoModelo: true,
      perPage: 24,
    };

    let arrayCarros =[];
    console.log("optiones en nuevos: ", options, "opciones en seminuevos: ", options);
    const carrosNuevos = await getCarrosNuevos(options)
    const carrosSeminevos = await getCarrosSeminuevos(optionsSeminuevos);

    setTimeout(() => {

      arrayCarros = carrosNuevos.concat(carrosSeminevos);
      console.log("ArrayCarros: ", arrayCarros);
      const modelo = arrayCarros?.map((a) => {return a?.nombre || a?.model});
      const marca = arrayCarros?.map((a) => {return a?.marca || a?.make});
      const years = arrayCarros?.map((a) => {return a?.year});

      let result = modelo?.filter((item,index)=>{ return modelo.indexOf(item) === index; })
      let result2 = marca?.filter((item,index)=>{ return marca.indexOf(item) === index; })
      let result3 = years?.filter((item,index)=>{ return years.indexOf(item) === index; })
      //console.log(result3);
      setDatosFiltros({
        ...datosFiltros, 
        modelo: result,
        marcas: result2,
        anios: result3
      })
      setLoading(false);
      setCar(arrayCarros);

    }, 1000)

  }

  const getCarrosNuevos = async (options) => {


    const response = await api.getNuevos(options).then((resp) => {

      const filterImage = resp?.data?.filter((obj) => { return obj.image !== null; })

      const posts = filterImage?.filter((p) => p.price > 0);

      const mapVersions = posts?.map((obj) => { return obj.slug })
      return mapVersions
      
    })


    let rays = [];

    await response?.map(async (obj2) => {

      await api.getCarroVersions(obj2).then((resp2) => {  
        
        var datosVersiones = resp2.data.versions;
  
        for(var i = 0; i < datosVersiones.length; i ++){
          datosVersiones[i].auto = "Nuevos";
          datosVersiones[i].galeria = resp2.data.gallery;
          datosVersiones[i].color = resp2.data.colors[1].name;
          datosVersiones[i].imagen = resp2.data.image;
          datosVersiones[i].year = resp2.data.year;
          datosVersiones[i].make = resp2.data.make;
          datosVersiones[i].model = resp2.data.model;
          datosVersiones[i].slug = resp2.data.slug;
          rays.push(datosVersiones[i]);
        }
        
      })  

    })
  
    return rays;

  }

  const getCarrosSeminuevos = async (options) => {
       //Faltaran lps filtros importantes
    const response = api.getSeminuevos(options).then((resp) => {

      //console.log("Esta es la respuesta recibida en seminuevos:", resp);

      const filterPicure = resp?.data?.filter((obj) => { return obj.picture !== null; })

      //arrayCarros.push(filterPicure);
      const posts = filterPicure?.filter((p) => p.precio > 0);

      for(var l = 0; l < posts.length; l++){
        posts[l].auto = "Seminuevos";
        posts[l].slug = `${posts[l].marca.toLowerCase()}-${posts[l].nombre.toLowerCase().split(" ").join("-")}-${posts[l].year}-${posts[l].id}`;
        posts[l].price = posts[l].precio;
        //console.log(posts[l]);
      }

      return posts;

    })
    .catch((error) => {console.log("Ah ocurrido un error: ", error);})

    return response
  }

  const onFilter = useCallback((filter, dato) => {
    //console.log("Pulsa: ", e)

      if(filter === "Tipo"){ tipo.indexOf(dato) === -1 ? tipo.push(dato) : tipo.splice(tipo.indexOf(dato),1); setDataInventario(dataInventario.concat(tipo)); }
      else if(filter === "Anio"){ anio.indexOf(dato) === -1 ? anio.push(dato) : anio.splice(anio.indexOf(dato),1); setDataAnio(dataAnio.concat(anio)); }
      else if(filter === "Unidad"){ unidad.indexOf(dato) === -1 ? unidad.push(dato) : unidad.splice(unidad.indexOf(dato),1); setDataUnidad(dataUnidad.concat(unidad)); }
      else if(filter === "Marca"){ marca.indexOf(dato) === -1 ? marca.push(dato) : marca.splice(marca.indexOf(dato),1); setDataMarca(dataMarca.concat(marca)); }
      else if(filter === "Modelo"){ modelo.indexOf(dato) === -1 ? modelo.push(dato) : modelo.splice(modelo.indexOf(dato),1); setDataModelo(dataModelo.concat(modelo)); }

  }, []);

  function filtroSelect (e) {
    console.log("tengo car: ", e.target.value)
    let select = e.target.value;
    let filterPrecio;
    if(select === "p-Menor" ) {
      filterPrecio = car.sort((a,b) => a.price - b.price);
    } else if (select === "p-Mayor" ) {
      filterPrecio = car.sort((a,b) => b.price - a.price);
    } else {
      filterPrecio = car;
    }
    //console.log("El sort: ", filterPrecio);
    setnewCars(filterPrecio);
  }

  function Buscar(){
    console.log(
      "Data Inventario: ", dataInventario, 
      "Anio: ", dataAnio,
      "Unidad: ", dataUnidad,
      "Marca: ", dataMarca,
      "Modelo: ", dataModelo,
      "Buscar: ", search
    );
    console.log(`?${dataInventario.length > 0 ? `inventario=${dataInventario}&` : ""}${dataAnio.length > 0 ? `anio=${dataAnio}&`: ""}${dataUnidad.length > 0 ? `unidad=${dataUnidad}&`: ""}${dataMarca.length > 0 ? `marca=${dataMarca}&`: ""}${dataModelo.length > 0 ? `modelo=${dataModelo}&`: ""}${search ? `search=${search}&`: ""}`)
    navigate(`?${dataInventario.length > 0 ? `type=${dataInventario}&` : ""}${dataAnio.length > 0 ? `year=${dataAnio}&`: ""}${dataUnidad.length > 0 ? `unity=${dataUnidad}&`: ""}${dataMarca.length > 0 ? `make=${dataMarca}&`: ""}${dataModelo.length > 0 ? `search=${dataModelo}&`: ""}${search ? `search=${search}&`: ""}`);

    //navigate('?search=aveo');
    setTimeout(() => {
        window.location.reload(false);
    }, 500)

  }
  console.log("newCars: ", newCars);
  const mapCarrosList = () => {

    var mapping = [];
    if(newCars){
      //console.log("Tengo newCars", currentItems);
      mapping = newCars?.map((obj, i) => {
        return(
          <ListView key={i} car={obj}/>
        )
      })
    } else {
      //console.log("Tengo car", currentItems);
      mapping = currentItems?.map((obj, i) => {
        return(
          <ListView key={i} car={obj}/>
        )
      })
    }
    return mapping
  }
  const mapCarrosGrid = () => {

    var mapping = [];
    if(newCars){
      //console.log("Tengo newCars", currentItems);
      mapping = newCars?.map((obj, i) => {
        return(
          <Col lg={4}>
            <GridView key={i} car={obj}/>
          </Col>
        )
      })
    } else {
      //console.log("Tengo car", currentItems);
      mapping = currentItems?.map((obj, i) => {
        return(
          <Col lg={4}>
            <GridView key={i} car={obj}/>
          </Col>
        )
      })
    }
    return mapping
  }

  return(
    <Layout props={props}>
      <Seo 
      title={`Rivero - Chevrolet Agencia Monterrey`} 
      description={` Rivero, Agencia Chevrolet Monterrey  Aveo, Beat, Spark, Onix, Cavalier, Trax - venta autos nuevos y seminuevos - Chevrolet Guadalupe, Santa Catarina, LindaVista`}
      keywords={`Rivero, Agencia Chevrolet Monterrey,agencia chevrolet, autos seminuevos, carro nuevo, carro chevrolet`}
      />

      <Container fluid className="pt-3">
        <Row className="px-3">
          <Col lg={2} style={{backgroundColor: '#343a40'}} className="mb-4">
            <FilterLayout datosFiltros={datosFiltros} Buscar={Buscar} setSearch={setSearch} onFilter={onFilter}/>
          </Col>
          <Col lg={10}>
            <Container className="mb-2">
                <Row>
                  <Col className="d-none d-lg-block">
                    <Button variant="outline-secondary" className="mx-1" onClick={() => setClickList(false)}><CiGrid2H size={20}/></Button>
                    <Button variant="outline-secondary" className="mx-1" onClick={() => setClickList(true)}><CiGrid41 size={20}/></Button>
                  </Col>
                  <Col lg={3}>
                    <Form.Select onChange={(e) => {filtroSelect(e)}}>
                      <option value="0">Selecciona...</option>
                      <option value="p-Menor">Precio - Menor a Mayor</option>
                      <option value="p-Mayor">Precio - Mayor a Menor</option>
                    </Form.Select>
                  </Col>
                </Row>
            </Container>
            <Container className="d-none d-lg-block">
            {!car ? (
              <PlaceHolderCar/>
            ) : 
              clickList ? (
                <Row>
                    {mapCarrosGrid()}
                </Row>
              ) : (
                mapCarrosList()
              )
            }  
          </Container>

          <Container className="d-xs-block d-sm-block d-md-block d-lg-none d-xl-none">
            {!car ? (
              <PlaceHolderCar/>
            ) : (
              mapCarrosGrid()
            )}  
          </Container>
          </Col>
        </Row>
      </Container>

      <Container fluid className="paginationContainer pt-3">
        <ReactPaginate
          nextLabel="next >"
          onPageChange={handlePageClick}
          pageRangeDisplayed={3}
          marginPagesDisplayed={2}
          pageCount={pageCountNew || pageCount}
          previousLabel="< previous"
          pageClassName="page-item"
          pageLinkClassName="page-link"
          previousClassName="page-item"
          previousLinkClassName="page-link"
          nextClassName="page-item"
          nextLinkClassName="page-link"
          breakLabel="..."
          breakClassName="page-item"
          breakLinkClassName="page-link"
          containerClassName="pagination"
          activeClassName="active"
          renderOnZeroPageCount={null}
        />
      </Container>

    </Layout>
  )
}